:root {
  --default-background: #343a40;
}

.App {
  background-color: var(--default-background);
}

.App-header {
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  border-bottom: 1px solid #4d4d4d;
  margin-bottom: 20px;
}

.App-content {
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: space-between;
  font-size: large;
  color: white;
}

.App-footer {
  min-height: 10vh;
  display: flex;
  margin-top: 20px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  border-top: 1px solid #4d4d4d;
}

.label-status-passed {
  background: #73974b;
}

.label-status-failed {
  background: #fd5a3e;
}

.label-status-broken {
  background: #ffd050;
}

.label-status-skipped {
  background: #aaa;
}

.label-status-unknown {
  background: #d35ebe;
}

.label-status-total {
  background: #007bff;
}

.label {
  vertical-align: baseline;
  letter-spacing: 1px;
  color: #fff;
  padding: 7px 9px 7px 9px;
  border-radius: 3px;
  margin: 4px;
}

.App-link {
  color: #61dafb;
}

.report-link {
  color: #f5f5f5;
  font-weight: bold;
}

.report-link:hover {
  color: #539bf5;
  text-decoration: none;
}

#reportsTable {
  max-width: 70%;
  border-radius: 6px 6px 6px 6px;
}

#reportsTable tr:first-child td:first-child th:first-child
{
  border-top-left-radius: 5px;
}

#reportsTable tr:first-child td:last-child th:last-child
{
  border-top-right-radius: 5px;
}

#reportsTable td {
  vertical-align: middle;
  padding: 10px 10px 10px 10px;
}

tr.passed {
  background-color: rgba(178, 222, 136, 0.2);
}

tr.passed:hover {
  background-color: rgba(150, 203, 99, 0.09);
}

tr.failed {
  background-color: rgba(236, 138, 118, 0.27);
}

tr.failed:hover {
  background-color: rgba(253, 90, 62, 0.12);
}

.sort-by-asc {
  left: 3px;
  display: inline-block;
  width: 0;
  height: 0;
  border: solid 6px transparent;
  margin: 6px 6px 1px 5px;
  background: transparent;
  border-bottom: solid 10px #ffffff;
  border-top-width: 0;
}

.sort-by-desc {
  left: 3px;
  display: inline-block;
  width: 0;
  height: 0;
  border: solid 6px transparent;
  margin: 6px 6px 1px 5px;
  background: transparent;
  border-top: solid 10px #ffffff;
  border-bottom-width: 0;
}
